/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/no-unused-vars */
import { CardTitle } from "@/components/ui/card";
import { Card, CardHeader, CardContent, Button } from "@mui/material";
import { Edit2 } from "lucide-react";
import StatusBadge from "../ListeSoutenances/StatusBadge";
import { Badge } from "@/components/ui/badge";

interface Soutenance {
    idSoutenance : number ;
    date: string ;
    time: string ;
    location: string ;
    nomGroupe: string ;
    juryNames:string[] ;
    status:string;
}

export default function SoutenancesPage() {
    //static soutenance data
    const soutenances: Soutenance[] = [
        {
            idSoutenance: 1,
            date: "2025-03-10",
            time: "10:00",
            location: "Salle 1",
            nomGroupe: "Groupe A",
            juryNames: ["Dr. Smith", "Dr. John"],
            status: "Scheduled",
        }
    ];
}

const studentGroup = "Groupe A" ;

const studentSoutenances = soutenances.filter((soutenances: any) => soutenance.nomGroupe === studentGroup);


<Card className="w-full">
  <CardHeader>
    <div className="flex items-center justify-between">
      <CardTitle className="text-2xl">Soutenances de votre groupe: {studentGroup}</CardTitle>
    </div>
  </CardHeader>
  <CardContent className="p-0">
    <div className="space-y-6">
      {studentSoutenances.map((soutenance) => (
        <div key={soutenance.idSoutenance} className="border-b pb-4">
          <div className="flex justify-between">
            <div className="font-semibold text-lg">
              <div className="text-xl">
                Date: {new Date(soutenance.date).toLocaleDateString("fr-FR", { day: "numeric", month: "long", year: "numeric" })}
              </div>
              <div>Time: {soutenance.time}</div>
              <div>Location: {soutenance.location}</div>
              <div>
                <Badge variant="secondary" className="font-medium text-lg">
                  {soutenance.nomGroupe}
                </Badge>
              </div>
            </div>
            <div className="text-lg">
              <StatusBadge status={soutenance.status} />
            </div>
          </div>
          <div className="mt-2">
            <div className="font-medium">Jury: {soutenance.juryNames.join(", ")}</div>
          </div>
          <Button variant="text" className="mt-4">
            <Edit2 className="h-5 w-5" />
          </Button>
        </div>
      ))}
    </div>
  </CardContent>
</Card>
